// Style Newsletters

.newsletters{
    color: #757575;
    .form-newsletter{
    }
    .des-newsletter{
        line-height: 24px;
    }
    .group-input-newsletter{
        display:block;
        padding: 10px 0px 0px;
    }
    input,.form-control{
        height: 40px;
        font-size: 14px;
    }
    #mc4wp{
        border: 1px solid rgba(255, 255, 255,0.2);
        background:#f8f8f8;
        height: 40px;
        min-width: 300px;
        width: 100%;
        border-radius:5px;
        display: block;
        padding:7px 15px;
        &.form-control::-moz-placeholder {
            color: #999999;
        }
    }
    .input-group-btn{

        display: block;
        width: 100%;
        white-space: normal;
    }
    .form-control::-moz-placeholder{
        font-size: 13px;
    }
    .btn-newsletter{
        margin-top: 10px;
        border: 1px solid rgba(255, 255, 255,0.2);
        background: $theme-color;
        border-radius:5px !important;
        color:#fff;
        height: 40px;
        width: 100%;
        letter-spacing: 1px;
        font-size: 14px;
        text-transform:uppercase;
        font-weight: 500;
        &:hover{
            background: $theme-color;
            color:#fff;
        }
    }
    .input-group{
        display: block;
    }
}
.mc4wp-response {
    bottom: -10px;
    left: 0;
    position: absolute;
    right: 0;
    text-align: center;
}
.widget_mc4wp_form_widget{
    > form{
        position: relative;
    }
}